/**
 * Icons
 */

.icon {
	background-repeat: no-repeat;
	background-color: transparent;
	display: inline-block;

	&.icon-arrow-right {
		@include size(10px, 16px);
		background-image: url("/src/img/icons/arrow-right.png");
		background-image: url("/src/img/icons/arrow-right.svg");
		background-position: 0 0; }

	&.icon-arrow-left {
		@include size(10px, 16px);
		background-image: url("/src/img/icons/arrow-left.png");
		background-image: url("/src/img/icons/arrow-left.svg");
		background-position: 0 0; }

	&.icon-erase {
		@include size(30px, 28px);
		background-image: url("/src/img/icons/erase.png");
		background-image: url("/src/img/icons/erase.svg");
		background-position: 0 0; }

	&.icon-file-pic {
		@include size(45px);
		background-image: url("/src/img/icons/file-pic.png");
		background-image: url("/src/img/icons/file-pic.svg");
		background-position: 0 0; }

	&.icon-edit-black {
		@include size(13px, 17px);
		background-image: url("/src/img/icons/icon-edit-black.png");
		background-image: url("/src/img/icons/icon-edit-black.svg");
		background-position: 0 0; }

	&.icon-remove-black {
		@include size(13px, 17px);
		background-image: url("/src/img/icons/icon-remove.png");
		background-image: url("/src/img/icons/icon-remove.svg");
		background-position: 0 0; }

	&.icon-remove-white-sm {
		@include size(7px, 9px);
		background-image: url("/src/img/icons/icon-remove-white.png");
		background-image: url("/src/img/icons/icon-remove-white.svg");
		background-position: 0 0; }

	&.icon-close-circle-red {
		@include size(18px);
		background-image: url("/src/img/icons/icon-close-circle-red.png");
		background-image: url("/src/img/icons/icon-close-circle-red.svg");
		background-position: 0 0; }

	&.icon-delete-red {
		@include size(17px, 19px);
		background-image: url("/src/img/icons/icon-delete-red.png");
		background-image: url("/src/img/icons/icon-delete-red.svg");
		background-position: 0 0; }

	&.icon-camera {
		@include size(34px, 29px);
		background-image: url("/src/img/icons/icon-camera.png");
		background-image: url("/src/img/icons/icon-camera.svg");
		background-position: 0 0; } }
